import requireurl from '../../requist/requist.js'
import uniPopup from "../../components/uni-popup/uni-popup.vue"

export default {
  components: {uniPopup},
	data() {
		return {
			timeout:60,
			showgetcode:0,
			timer:null,
			oldphone:'',
			newsphone:'',
			usercode:'',
			authentication:false,
			type:'center',
			errortit:'',
		}
	},
	methods: {
		
		//修改手机号获取验证码
		getcode(){
			var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
			if(!myreg.test(this.newsphone) || this.newsphone.length < 11){
				return false
			}
			let data = {
				mobile:this.newsphone
			}
			requireurl.request('/users/codes/',data,this.getcodesuccess,this.file)
		},
		//获取验证码回调
		getcodesuccess(e){
			if(e.statusCode === 200){
				this.showgetcode = 1
				this.timer = setInterval(()=>{
					this.timeout--;
					if(this.timeout <= 0){
						clearInterval(this.timer)
						this.showgetcode = 2
						this.timeout = 59
					}
				},1000)
			}
		},
		
		//修改手机号提交
		initmobile(){
			let data = {
				mobile:this.newsphone,
				code:this.usercode
			}
			requireurl.getPutData('/users/mobile/',data,this.changemobile,this.file)
		},
		//修改手机号回调
		changemobile(e){
			if(e.statusCode === 200){
				let storeobj = uni.getStorageSync('storage_user')
				storeobj.mobile = this.newsphone
				storeobj.token = e.data.token
				uni.setStorageSync('storage_user',storeobj);
				uni.navigateBack({
				    delta:1
				});
			}else{
				this.errortit = e.data.err
				this.authentication = true
			}
		}
	},
	destroyed() {
		if(this.timer){
			clearInterval(this.timer)
		}
	}
}
